'use client';

import { Menu, House, ChartGantt, FolderKanban, Logs, LogOut } from "lucide-react"
import { useState } from "react"

const sideBarItems = [
    { icon: <House/>, label: 'Inicio', link: '/inicio' },
    { icon: <ChartGantt/>, label: 'Gantt', link: '/gantt' },
    { icon: <FolderKanban/>, label: 'Proyectos', link: '/projects' },
    { icon: <Logs/>, label: 'Logs', link: '/logs' },
    { icon: <LogOut/>, label: 'Salir', link: '/logout', extraClass: 'mt-80'}
];

export default function Sidebar() {
    const [isCollapsed, setIsCollapsed] = useState(false);

    return (
        <aside className={`flex flex-row h-screen bg-white transition-all duration-300 ${isCollapsed ? 'w-16' : 'w-64'}`}>
            <nav className="h-full flex flex-col bg-white shadow-sm">
                <div className="p-4 pb-2 flex justify-between items-center">
                    <button 
                        onClick={() => setIsCollapsed(!isCollapsed)} 
                        className="p-1.5 rounded-lg bg-gray-50 hover:bg-gray-100"
                    >
                        <Menu/>
                    </button>
                </div>

                <div className="flex flex-col ml-4 mt-10">
                    {/* Sidebar items */}
                    <div className='flex flex-col gap-7'>
                        {sideBarItems.map(({ icon, label, extraClass}, index) => (
                            <div 
                                key={label} 
                                role="tab" 
                                className={`flex gap-3 py-2 cursor-pointer ${extraClass || ''}`}
                            >
                                {icon}
                                {!isCollapsed && <span>{label}</span>}
                        </div>
                        ))}
                    </div>
                </div>
            </nav>
        </aside>
    )
}